
#app .product-lists {
  margin: 10px auto;
  width: 1200px;
}
#app .product-lists .product-wrapper {
  width: 1220px;
}
#app .product-lists .product-wrapper .product-cell {
  float: left;
  width: 20%;
}
#app .product-lists .product-wrapper .product-cell .product-cell-main {
  position: relative;
  margin-right: 20px;
  margin-bottom: 20px;
  border: 1px solid #ddd;
}
#app .product-lists .product-wrapper .product-cell .product-cell-main .product-img {
  display: block;
  margin: 30px auto 10px;
  width: 180px;
  padding: 0 13px;
}
#app .product-lists .product-wrapper .product-cell .product-cell-main .product-img img {
  display: block;
  width: 180px;
  height: 180px;
}
#app .product-lists .product-wrapper .product-cell .product-cell-main .product-name {
  display: inline-block;
  line-height: 1.5;
  padding: 0 13px;
}
#app .product-lists .product-wrapper .product-cell .product-cell-main .price {
  margin-top: 8px;
  margin-bottom: 8px;
  text-align: center;
  font-size: 20px;
  padding: 0 13px;
  color: #f00;
}
#app .product-lists .product-wrapper .product-cell .product-cell-main .buy-action {
  height: 30px;
  line-height: 30px;
  border-top: 1px solid #ddd;
}
#app .product-lists .product-wrapper .product-cell .product-cell-main .buy-action a {
  display: inline-block;
  width: 49%;
  border-right: 1px solid #ddd;
  text-align: center;
}
#app .product-lists .product-wrapper .product-cell .product-cell-main .buy-action span {
  display: inline-block;
  width: 49%;
  text-align: center;
}
#app .product-lists .product-wrapper .product-cell .product-cell-main .product-state {
  position: absolute;
  top: 0;
  left: 50%;
  width: 70px;
  margin-left: -35px;
  height: 15px;
  line-height: 15px;
  text-align: center;
}
#app .product-lists .product-wrapper .product-cell .product-cell-main .product-state.shoufa {
  background: #fc3884;
}
#app .product-lists .product-wrapper .product-cell .product-cell-main .product-state.tehui {
  background: #01abdf;
}
#app .product-lists .product-wrapper .product-cell .product-cell-main .product-state.xinpin {
  background: #24ca44;
}
#app .pagination {
  margin: 10px auto;
  width: 1200px;
  font-size: 0;
}
#app .pagination a {
  margin-right: 4px;
  display: inline-block;
  padding: 8px 11px;
  border: 1px solid #ddd;
  font-size: 14px;
}


